import React from "react";
import { makeStyles, Typography, Box, Link, Button } from "@material-ui/core";
import { Language, Facebook, Instagram } from "@material-ui/icons";
import { experienceList } from "../../data";
import IconBtn from "../../components/IconBtn";
import { useTranslation } from "react-i18next";

const StyledSections = () => {
  const { t } = useTranslation();
  const classes = useStyles();

  return (
    <div className={classes.root}>
      {experienceList.map((elem) => (
        <div key={elem.id} className={classes.section}>
          <Box mb={4}>
            <Typography variant="h5">
              {t(`experience_${elem.id}_job`)} @{" "}
              <Link
                href={
                  elem.links.website ||
                  elem.links.facebook ||
                  elem.links.instagram
                }
                color="primary"
              >
                {elem.company}
              </Link>
            </Typography>
            <Typography variant="body2" color="textSecondary" fontSize="14">
              {t(`experience_${elem.id}_duration`)}
            </Typography>
          </Box>
          <Box mb={4}>
            <Typography variant="body1" color="textPrimary">
              {t(`experience_${elem.id}_overview`)}
            </Typography>
          </Box>
          <Box>
            {elem.links.website && (
              <IconBtn
                icon={Language}
                fontSize={28}
                m={1}
                href={elem.links.website}
              />
            )}
            {elem.links.facebook && (
              <IconBtn
                icon={Facebook}
                fontSize={28}
                m={1}
                href={elem.links.facebook}
              />
            )}
            {elem.links.instagram && (
              <IconBtn
                icon={Instagram}
                fontSize={28}
                m={1}
                href={elem.links.instagram}
              />
            )}
          </Box>
        </div>
      ))}
    </div>
  );
};

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.main,
    display: "flex",
    flexDirection: "column",
    width: "100%",
    padding: theme.spacing(3),
  },
  section: {
    marginBottom: theme.spacing(4),
    border: `1px solid ${theme.palette.divider}`,
    borderRadius: theme.shape.borderRadius,
    padding: theme.spacing(3),
  },
}));

export default StyledSections;
